.statistics {
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  box-shadow: 0 5px 10px 3px rgb(207, 207, 207);
}

.title {
}

.statList {
  padding-right: 0;
  padding-left: 0;
  display: flex;
  flex-direction: row;
  width: 100%;
}

.item {
  color: #fff;
  list-style: none;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding-top: 5px;
  padding-bottom: 10px;
}

.label {
  font-size: 14px;
}

.percentage {
  font-size: 20px;
}
